<template>
  <div class="deblur-gan" v-loading="!model">
    <el-row>
      <canvas
        id="deblur-gan-hidden"
        ref="deblurGANHidden"
        width="256"
        height="256"
      />
      <canvas
        id="deblur-gan-result"
        ref="deblurGANResult"
        width="256"
        height="256"
      />
    </el-row>
    <el-row>
      <el-button @click="$refs.uploader.click()">
        打开图片
      </el-button>
      <input ref="uploader" type="file" @change="handleFileChange" hidden />
    </el-row>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';
import labels from './ImageNetLabels';

export default {
  name: 'DeblurGAN',
  data() {
    return {
      model: null,
    };
  },

  methods: {
    handleFileChange() {
      if (!this.$refs.uploader.files.length) {
        return;
      }

      const hiddenCanvas = this.$refs.deblurGANHidden;
      const file = this.$refs.uploader.files[0];
      const imgUrl = URL.createObjectURL(file);
      const img = new Image();
    
      img.onload = () => {
        URL.revokeObjectURL(img.src);
        hiddenCanvas.getContext("2d").drawImage(img, 0, 0, 256, 256);

        this.makePrediction(hiddenCanvas);
      };

      img.src = imgUrl;
    },

    async makePrediction(data) {
      const deprocessed = tf.tidy(() => {
        const x = tf.sub(tf.div(tf.fromPixels(data).asType('float32'), 255.), 0.5).expandDims();
        const prediction = this.model.predict(x).squeeze();
        const deprocessed = tf.add(tf.div(prediction, 2), 0.5).clipByValue(0, 1);
        return deprocessed;
      });
      tf.toPixels(deprocessed, this.$refs.deblurGANResult);
    },
  },

  async mounted() {
    this.model = await tf.loadModel('models/deblur_gan/model.json');
  },
}
</script>

<style>
.deblur-gan .el-row {
  margin-bottom: 16px;
}
</style>
